	module.exports = {"data":{"post":{"html":"<p>Happy Birthday to my younger brother Steve in Montevideo, Minnesota. Hey, we though we could catch you yesterday during the Vikings game, but you were just pulling out of the driveway as we called!</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh0103.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"crh0103.jpg: \" /></p>\n<p>Fittingly, the first baby of the new year in Hamburg was born to Turkish parents. Baby Izzmet is the first foreign child born in Hamburg to automatically gain German citizenship, under the new naturalization law that took effect on 1 January.</p>","fields":{"title":"Happy Birthday Steve","date":"03 Jan 2000","url":"https://papascott.de/2000-01-03-happy-birthday-steve/"}}},"pathContext":{"slug":"/2000-01-03-happy-birthday-steve/","prev":{"fields":{"title":"Christopher is home","date":"02 Jan 2000","slug":"/2000-01-02-christopher-is-home/"}},"next":{"fields":{"title":"2nd Night Home","date":"04 Jan 2000","slug":"/2000-01-04-2nd-night-home/"}}}}

